import axios from "axios";

const TokenName = "hyUser";
const priviligeId = "1";

function setToken(value, options = { secure: true, "max-age": 3600 }) {
  options = {
    path: "/",
    // 필요한 경우, 옵션 기본값을 설정할 수도 있습니다.
    ...options,
  };

  if (options.expires instanceof Date) {
    options.expires = options.expires.toUTCString();
  }

  let updatedCookie =
    encodeURIComponent(TokenName) + "=" + encodeURIComponent(value);

  for (let optionKey in options) {
    updatedCookie += "; " + optionKey;
    let optionValue = options[optionKey];
    if (optionValue !== true) {
      updatedCookie += "=" + optionValue;
    }
  }

  document.cookie = updatedCookie;
}
function getToken() {
  let matches = document.cookie.match(
    new RegExp(
      "(?:^|; )" +
        TokenName.replace(/([\\.$?*|{}\\(\\)\\[\]\\\\/\\+^])/g, "\\$1") +
        "=([^;]*)"
    )
  );
  return matches ? decodeURIComponent(matches[1]) : undefined;
}
function deleteToken() {
  document.cookie = TokenName + "=; Max-Age=-99999999;";
}
export function logOut() {
  deleteToken();
  return true;
}
export async function logIn() {
  const auth = {
    data: "asdfasdf",
  };
  if (auth.error === true) {
    throw Error(auth.message);
  }

  setToken(auth.data.token);
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({
        state: "00",
      });
    }, 1000);
  });
}
export async function auth() {
  // uid, password
  const token = getToken();
  return new Promise((resolve) => {
    setTimeout(() => {
      if (token) {
        resolve({
          state: "00",
        });
      } else {
        resolve({
          state: "01",
        });
      }
    }, 1000);
  });
}
export async function role() {
  return new Promise((rej) => {
    rej({
      state: "00",
      data: ["1", "2", "3"],
    });
  });
}
export function user_users(queryString) {
  return axios({
    method: "GET",
    url:
      `https://13.124.69.255:90/api/user/GetUser_List?priviligeId=${priviligeId}` +
      queryString,
    mode: "no-cors",
  });
}
export function create_user_user(body) {
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/user_detail/Save_User",
    data: body,
    mode: "no-cors",
  });
}
export function update_user_user(body) {
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/user_detail/Save_User",
    data: body,
    mode: "no-cors",
  });
}
export function delete_user_user(body) {
  return axios({
    method: "DELETE",
    url: "https://13.124.69.255:90/api/user/Delete_User",
    data: { idxs: [`${body.user_idx}`] },
    mode: "no-cors",
  });
}
